























import Vue from 'vue'
import Component from 'vue-class-component'
import { upload_dist } from '@/service/apis.ts'
import mTStatus from '@/views/system-configuration/version-updating/status.vue'
import mButton from '@/views/system-configuration/version-updating/button.vue'

@Component({
    components: { mTStatus, mButton }
})
export default class App extends Vue {
 $refs: {
        checkboxElement: HTMLFormElement
    }
    // 初始化数据


    upload_dist = ''

    columns1 = [
                {
                    type: 'index',
                    width: 60,
                    title: '序号',
                    align: 'center'
                },
                {
                    title: '版本名称',
                    key: 'dist_path',
                    width: 800
                },
                {
                    title: '操作人',
                    align: 'center',
                    key: 'operator'
                },
                {
                    title: '状态',
                    align: 'center',
                    key: 'status',
                    render: (h, params) =>{
                    return h (mTStatus, {
                        props: {
                            row: params.row
                        }
                    })
                }
                },
                {
                    title: '更新日期',
                    align: 'center',
                    key: 'updated_at'
                },
                {
                    title: '操作',
                    align: 'center',
                    render: (h, params) => {
                    return h(mButton, {
                        props: {
                            row: params.row
                        },
                        on: {
                            'removeSend' : (val) => {
                                this.handRemove(val)
                            },
                            'showSend' : (val) => {
                                this.handShow(val)
                            }
                        }
                    })
                }
                }
                
            ]
            data1 = []
    
    defaultList() {
        new Vue.prototype.$http('DIST').get('list').then(res=>{
            if(res) {
                this.data1 = res.data
                
            }
        })
    }

    handShow(id) {

    }

    handRemove(id) {

    }

    created() {

        this.defaultList()
        this.upload_dist = upload_dist
    }

    handleFormatError(file) {
        Vue.prototype.$Message.warning('请上传zip格式的文件！')
    }

    handleMaxSize() {
        Vue.prototype.$Message.warning('请上传小于150M的文件！')
    }

    handleSuccess(res, file) {
        Vue.prototype.$Message.success('上传成功！')
        this.defaultList();

        this.$refs['dist'].clearFiles();
        // console.log(res, file)
    }

}



