












import Vue from 'vue'
import {Component, Prop, Emit} from 'vue-property-decorator'
@Component({

})
export default class App extends Vue {
    // 初始化数据
    @Prop({default: {}})
    row: Object

    created() {
    }

    /**
     * 向父组件传值， 编辑
     */
    show(){
        this.$emit('showSend', this.row['id']);
    }

    /**
     * 向父组件传值， 删除
     */
    ok() {
        this.$emit('removeSend', this.row['id']);
    }
}



